
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import vuxScroller from 'vux/src/components/scroller/index.vue'
import Marquee from 'vux/src/components/marquee/marquee.vue'
import MarqueeItem from 'vux/src/components/marquee/marquee-item.vue'
import Panel from 'vux/src/components/panel/index.vue'

import {mapState} from 'vuex'


var  refreshInfo =['A','B01','B02','B03','B04','B05','B06','B07','B08','B09']
var  keyValue = 'A'
var  key = 0 
var  start = 0
var  end = start + 9 

export default {
name: 'App',
components: {
ViewBox,
XHeader,
Tabbar,
TabbarItem,
Tab,
TabItem,
Swiper,
vuxScroller,
Marquee,
MarqueeItem,
Panel
},
created(){
this.$store.dispatch('getInitData')
},
data(){
return {
swiperIndex:0,//轮播图索引
infiniteTimer:null
}
},
computed:{
...mapState(['swiperList','marqueeList','panelList','initDataFinished'])
},
methods: {
refreshKey(){
keyValue = refreshInfo[key]
key++
if(key > refreshInfo.length - 1){
key = 0
}
},
refreshStart(){
start += 9
end = start + 9
},
refresh() { //下拉刷新

this.$store.dispatch('pullRefresh',{refreshKey:this.refreshKey,keyValue})
.then((n)=>{
this.$refs.vueScroller.finishPullToRefresh()
this.$vux.toast.text(`更新了${n}条新闻`,'top')
})

},
infinite(){ //上拉下载更多
if(!this.initDataFinished){ //初始数据没加载完不加载更多
this.$refs.vueScroller.finishInfinite()
return
}
clearTimeout(this.infiniteTimer)
this.infiniteTimer = setTimeout(()=>{
this.$store.dispatch('pushLoadMore',{keyValue,refreshStart:this.refreshStart,start,end})
.then((res)=>{
this.$refs.vueScroller.finishInfinite()
this.start += 9
})
},1000)
}
}
}



